<template>
  <div id="evaluate">
    <div class="doctorDetail clearfix">
      <img src="../../assets/user/evaluate/doccom_photo.png" alt class="leftDocImg">
      <p class="docCon">
        <span class="docName">{{docName}}</span>
        <span class="docJob">{{docJob | typeNumber }}</span>
      </p>
      <p class="hospitalName">{{hospitalName}}</p>
    </div>
    <div class="content">
      <div class="starBox clearfix">
        <div
          class="star"
          v-for="(item,index) in tabs"
          :class="{active:item}"
          v-bind:key="index"
          @click="changeStar(index)"
        ></div>
      </div>
      <div class="evaluateTip clearfix">
        <span
          class="tipsE"
          v-for="(item,index) in tipsE"
          :class="[item.isActive ? active : '']"
          :ref="index"
          v-bind:key="index"
          @click="changeTips(item.value,item.isActive)"
        >{{item.name}}</span>
      </div>
    </div>
    <div class="fontEvaluate">
      <textarea
        class="formInput"
        name
        v-model="evaluateFont"
        id
        cols="30"
        rows="10"
        placeholder="请输入对医生的评价…"
      ></textarea>
      <p class="tipsNormal tipsFont" v-if="fontNumShow">
        <span class>{{fontNum}}</span>/299
      </p>
      <p class="tipsFail tipsFont" v-else>超出299字</p>
    </div>
    <div class="renameE" :class="{active: isNotMe}" @click="()=>{isNotMe = !isNotMe}">
      <span class>匿名评价</span>
    </div>
    <big-btn :content="content" :topNum="topNum" :rotateShow="rotateShow" @childClick="sureSubmit"></big-btn>
    <div v-show="evaluateDialog">
      <div class="dialogBk"></div>
      <div class="dialogContent">
        <p>
          <img src="../../assets/doccom_icon_success@2x.png" alt>
        </p>
        <p class="fontSuc">您已评价成功</p>
        <p class="times">{{evaluateTimes}}s后将返回上一页</p>
      </div>
    </div>
    <div style="display: none;">{{docNameComputed}}</div>
  </div>
</template>

<script>
///appointment/doctorDetails/:doctorId/:doctorName/:doctorLevel/:curDoctorJobTitle

import bigBtn from "./userCommon/bigBtn";
import store from "@/vuex/index";
import { apiUrl } from "@/common/js/api";
import { ajaxPost } from "@/common/js/public";
import { Notify } from "vant";
export default {
  name: "evaluate",
  components: {
    bigBtn
  },
  data() {
    return {
      docName: "",
      docJob: "",
      hospitalName: "",
      content: "提交评价",
      topNum: "1.38rem",
      rotateShow: false,
      active: "active", //active显示
      fontNum: 0, //字体数量
      fontNumShow: true, //字体有没有超过300字
      tabs: [0, 0, 0, 0, 0], //循环星星
      numCount: 0, //总积分
      isNotMe: true, //匿名评价
      evaluateTimes: 3, //评价倒计时
      evaluateDialog: false, //评价成功显示隐藏
      evaluateFont: "",
      tipsE: [],
      tipsNum: [],
      numOne: true,
      numFive: true,
      newArr: [],
      timers: null //定时器
    };
  },
  computed:{
     docNameComputed() {
        let id = JSON.parse(window.localStorage.getItem('gmoduleEvaluate')).id;
        let userId = JSON.parse(window.localStorage.getItem('gmoduleEvaluate')).userId;
        let doctId = JSON.parse(window.localStorage.getItem('gmoduleEvaluate')).doctId;
        let doctName = JSON.parse(window.localStorage.getItem('gmoduleEvaluate')).doctName;
        let feeType = JSON.parse(window.localStorage.getItem('gmoduleEvaluate')).feeType;
        let hospName = JSON.parse(window.localStorage.getItem('gmoduleEvaluate')).hospName;
        if(id){
          store.commit("gmodule/changeEvaluateDetail", {
            id: id,
            userId: userId,
            doctId: doctId,
            doctName: doctName,
            feeType: feeType,
            hospName: hospName,
				  });
        }
        return store.state.gmodule.curEvaluateDetail
      }
  },
  mounted() {
    this.docName = store.state.gmodule.curEvaluateDetail.doctName;
    this.docJob = store.state.gmodule.curEvaluateDetail.feeType;
    this.hospitalName = store.state.gmodule.curEvaluateDetail.hospName;
    console.log(store.state.gmodule.curEvaluateDetail.doctId.toString())
    ajaxPost(
      //回显医生标签
      apiUrl.getDoctorWork,
      {
        doctorId: store.state.gmodule.curEvaluateDetail.doctId.toString()
        //doctorId : "1"
      },
      function(data) {
        console.log(data);
        var data = data.data.body;
        console.log("data", data);
        if (data.resultCode == 1000) {
          console.log(data.data.isExpert);
          console.log(data.data.jobTitle);
          store.commit("gmodule/changeEvaluateDetail", {
            id: store.state.gmodule.curEvaluateDetail.id,
            userId: store.state.gmodule.curEvaluateDetail.userId,
            doctId: store.state.gmodule.curEvaluateDetail.doctId,
            doctName: store.state.gmodule.curEvaluateDetail.doctName,
            feeType: store.state.gmodule.curEvaluateDetail.feeType,
            hospName: store.state.gmodule.curEvaluateDetail.hospName,
            isExpert: data.data.isExpert,
            jobTitle: data.data.jobTitle
				  });
        } else if (data.resultCode == 1001) {
          console.log("参数为空");
        } else if (data.resultCode == 1110) {
          console.log("业务异常");
        }
      }, function(error) {
        console.log(error);
      })
    var that = this;
    ajaxPost(
      //回显医生标签
      apiUrl.showHospitalTag,
      {
        doctLable: "doct_lable"
      },
      function(data) {
        console.log(data);
        var data = data.data.body;
        console.log("data", data);
        if (data.resultCode == 1000) {
          that.tipsE = data.data;
          that.tipsE.map((obj, index) => {
            that.$set(obj, "isActive", false);
          });
        } else if (data.resultCode == 1001) {
          console.log("参数为空");
        } else if (data.resultCode == 1110) {
          console.log("业务异常");
        }
      }, function(error) {
        console.log(error);
      })
  },
  beforeDestroy() {
    clearInterval(this.timers);
    this.timers = null;
  },
  methods: {
    notifyIt(msg, time = 1000) {
      this.$notify({
        message: msg,
        duration: time
      });
    },
    sureSubmit: function() {
      if (!this.numCount) {
        this.notifyIt("请选择星星评价医生");
        return false;
      }
      this.newArr = []; //存储tags
      for (let i = 0; i < this.tipsE.length; i++) {
        if (this.tipsE[i].isActive) {
          this.newArr.push(this.tipsE[i].value);
        }
      }
      this.rotateShow = true;
      var _this = this;
      ajaxPost(
        //评价医生
        apiUrl.addComment,
        {
          "doctId": store.state.gmodule.curEvaluateDetail.doctId,
          "userId":  JSON.parse(sessionStorage.getItem("userInfo")).userId,
          "userName": JSON.parse(sessionStorage.getItem("userInfo")).userName,
          "tags": _this.newArr,  //评价标签数组  标签value
          "comments": _this.evaluateFont,  // 评论内容
          "anonymity": _this.tfToNum(_this.isNotMe),  //是否匿名
          "orderId": store.state.gmodule.curEvaluateDetail.id,  //单号id
          "level": _this.numCount.toString()  //评价几颗星
          // doctId: "6",
          // userId: "229caed738ac4100ace0016951edb028",
          // userName: "王小明",
          // tags: [1, 2, 3], //评价标签数组  标签value
          // comments: "医生态度好", // 评论内容
          // anonymity: "0", //是否匿名
          // orderId: "e865749569694e4f8225c3f967a95f3a", //单号id
          // level: "4" //评价几颗星
        },
        function(data) {
          console.log(data);
          var data = data.data.body;
          console.log("data", data);
          if (data.resultCode == 1000) {
            _this.openDialog();
          } else if (data.resultCode == 1001) {
            _this.notifyIt("参数为空");
          }
          _this.rotateShow = false;
        },
        function(error) {
          _this.notifyIt("网络繁忙");
        }
      );
      console.log("提交评价");
    },
    openDialog() {
      this.evaluateDialog = true;
      this.timers = setInterval(() => {
        this.evaluateTimes--;
        if (this.evaluateTimes < 0) {
          this.evaluateTimes = 0;
          this.$router.go(-1);
        }
      }, 1000);
    },
    tfToNum(a) {
      return a ? "1" : "0";
    },
    changeStar: function(num) {
      this.resetTabs();
      for (let i = 0; i < num + 1; i++) {
        this.tabs[i] = 1;
        this.numCount += this.tabs[i];
      }
    },
    resetTabs: function() {
      this.numCount = 0;
      this.tabs = [0, 0, 0, 0, 0];
    },
    changeTips: function(num, active) {
      this.tipsE[num].isActive = !this.tipsE[num].isActive;
      console.log(this.tipsE);
    }
  },
  watch: {
    evaluateFont(newVal) {
      if (newVal.length > 299) {
        this.fontNumShow = false;
        this.evaluateFont = this.evaluateFont.substring(0, 300);
      } else {
        this.fontNumShow = true;
      }
      this.fontNum = newVal.length;
    }
  },
  filters: {
    typeNumber: function(myInput) {
      var result;
      if (myInput == 0) {
        result = "主治医师";
      }else{
        result = "专家";
      }
      return result;
    }
  }
};
</script>

<style scoped lang="less">
.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.clearfix {
  zoom: 1;
}
#evaluate {
  padding-bottom: .98rem;
  .doctorDetail {
    box-sizing: border-box;
    width: 100%;
    padding: 0.26rem 0.32rem 0.38rem 0.32rem;
    border-bottom: 1px solid #f7f7f8;
    .leftDocImg {
      width: 0.68rem;
      height: 0.8rem;
      float: left;
      margin-right: 0.4rem;
    }
    .docCon {
      float: left;
      width: 82%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      .docName {
        font-family: "PingFangSC-Regular";
        float: left;
        font-size: 0.3rem;
        line-height: normal;
        padding: 0.04rem 0;
        color: #272727;
        margin-right: 0.12rem;
      }
      .docJob {
        opacity: 0.6;
        font-family: PingFangSC-Regular;
        font-size: 0.2rem;
        float: left;
        color: #272727;
        line-height: normal;
        padding: 0.08rem 0;
      }
    }
    .hospitalName {
      float: left;
      opacity: 0.8;
      font-family: PingFangSC-Regular;
      font-size: 0.24rem;
      color: #272727;
    }
  }
  .content {
    padding: 0.44rem 1.12rem;
    padding-bottom: 0.16rem;
    .starBox {
      width: 5.2rem;
      height: 0.55rem;
      margin-bottom: 0.55rem;
      .star {
        width: 0.55rem;
        height: 0.55rem;
        background: url("../../assets/user/evaluate/doccom_icon_star_pre@2x.png")
          no-repeat;
        background-size: 100% 100%;
        float: left;
        margin-right: 0.26rem;
        &:first-of-type {
          margin-left: 0.7rem;
        }
      }
      .star.active {
        background: url("../../assets/user/evaluate/doccom_icon_star@2x.png")
          no-repeat;
        background-size: 100% 100%;
      }
    }
    .evaluateTip {
      .tipsE {
        background: #f1f1f1;
        border-radius: 4px;
        font-family: "PingFangSC-Regular";
        font-size: 0.26rem;
        color: #b1afaf;
        line-height: normal;
        padding: 0.06rem 0.2rem;
        display: block;
        margin-right: 0.56rem;
        float: left;
        margin-bottom: 0.28rem;
        &:nth-of-type(3n) {
          margin-right: 0;
        }
      }
      .tipsE.active {
        background: #f4f8fe;
        color: #3391e8;
      }
    }
  }
  .fontEvaluate {
    width: 100%;
    box-sizing: border-box;
    padding: 0 0.3rem 0.3rem 0.3rem;
    position: relative;
    textarea::-webkit-input-placeholder {
      color: #b1afaf;
      font-size: 0.28rem;
      font-family: "PingFangSC-Regular";
    }
    textarea::-moz-placeholder {
      color: #b1afaf;
      font-size: 0.28rem;
      font-family: "PingFangSC-Regular";
    }
    textarea:-moz-placeholder {
      color: #b1afaf;
      font-size: 0.28rem;
      font-family: "PingFangSC-Regular";
    }
    textarea:-ms-input-placeholder {
      color: #b1afaf;
      font-size: 0.28rem;
      font-family: "PingFangSC-Regular";
    }
    .formInput {
      width: 100%;
      height: 3.14rem;
      box-sizing: border-box;
      padding: 0.2rem 0.22rem;
      border: 1px solid #f1f1f1;
      border-radius: 4px;
    }
    .tipsFont {
      position: absolute;
      right: 0.4rem;
      bottom: 0.4rem;
      opacity: 0.6;
      font-family: "PingFangSC-Regular";
      font-size: 0.28rem;
      line-height: normal;
      padding: 0.1rem;
      color: #272727;
    }
    .tipsFail {
      color: #ff941f;
      opacity: 0.8;
    }
  }
  .renameE {
    padding: 0 0.3rem;
    font-family: "PingFangSC-Regular";
    font-size: 0.28rem;
    color: #272727;
    background: url("../../assets/user/evaluate/doccom_icon_select_pre@2x.png")
      no-repeat left center;
    background-size: 0.32rem 0.32rem;
    width: 1.3rem;
    float: right;
    span {
      padding: 0.06rem 0 0.06rem 0.14rem;
      line-height: normal;
      opacity: 0.6;
    }
  }
  .renameE.active {
    background: url("../../assets/user/evaluate/doccom_icon_select@2x.png")
      no-repeat left center;
    background-size: 0.32rem 0.32rem;
  }
  .dialogBk {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    z-index: 8;
  }
  .dialogContent {
    width: 4.2rem;
    height: 2.6rem;
    background: #ffffff;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate( -50% , -50%);
    z-index: 9;
    p {
      img {
        width: 0.56rem;
        height: 0.56rem;
        margin: 0.48rem 0 0.16rem 1.76rem;
      }
    }
    .fontSuc {
      text-align: center;
      line-height: normal;
      box-sizing: border-box;
      padding: 0.08rem 0;
      font-size: 0.28rem;
    }
    .times {
      text-align: center;
      line-height: normal;
      opacity: 0.6;
      font-family: PingFangSC-Regular;
      font-size: 0.24rem;
      color: #272727;
    }
  }
}
</style>


